.App {
  text-align: center;
  background-color: #f7f7f7;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/**
* THE REAL DEAL
*/

.projects-container {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;

  flex-wrap: wrap;
  justify-content: space-around;
}
.card {
  border: 1px solid #ababab94;
  width: calc(25% - 18px);
  height: 444px;
  background-color: white;
  min-width: 300px;
  margin: 8px;
  flex-grow: 1;

  text-align: justify;
  
  overflow: hidden;
  position: relative;
}

.card .main-card img{
  width: 100%;
  height: 230px;
  object-fit: cover;
}

.card .card-header {
  padding: 4px;
}

.card .card-header > div{
  font-size: large;
  font-weight: bolder;
  color: #4e96ab;
  text-align: left;
  width: calc(100% - 16px);
  display: inline-block;
}
.card .card-header > img{
  width: 16px;
  height: 16px;
}
.card .main-card .card-description{
  padding: 4px;
}

.hidden-card {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: white;
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: .5s ease;
}

.hidden-card.slideup{
  height: 100%;
}

.card-footer {
  position: absolute;
  left: 0;
  bottom: 0;
  width: calc(100% - 12px);
  border-top: 1px solid #dde6e8;
  padding: 6px;
  text-align: center;
}

.card-footer img{
  height: calc(100% - 12px);
  padding: 6px;
}

.card{
  -webkit-box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
  -moz-box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
}


.card-action {
  width: 55.5px;
  height: 55.5px;
  margin-right: 20px;
  cursor: pointer;
  display: inline-block;
  overflow: hidden;
  -webkit-tap-highlight-color: transparent;
  vertical-align: middle;
  z-index: 1;
  transition: all 0.3s ease-out;
  line-height: 60px;
  border-radius: 50%;
  background-color: #5496ab  !important;
}


.card-action svg{
  font-size: 1.6rem;
  width: inherit !important;
  color: #FFF;
}


/* Tooltip text */
.card-action .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: #487b7f;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  line-height: 20px;
 
  /* Position the tooltip text - see examples below! */
  position: absolute;
  top: -30px;
  margin-left: -30px;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.card-action:hover .tooltiptext {
  visibility: visible;
}

.tooltiptext::after {
  content: " ";
  position: absolute;
  top: 100%; /* At the bottom of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #487b7f transparent transparent transparent;
}


.container{
  padding-top: 10px;
}
.area{
  margin-top: 4px;
  margin-bottom: 8px;
  text-align: justify;
}
.area-title{
  padding: 10px 10px;
  color: white;
  text-align: left;
  font-weight: 700;
  font-size: 25px;
}
.area-content{
  padding: 0px 10px;
}